<template>
  <div class="access-by-mnemonic-container page-container">
    <div class="title">
      <h2>{{ $t('common.accessMyWallet') }}</h2>
      <h5>
        {{ $t('accessWallet.pleaseTypeInMnemonicPhrase') }}
        {{ $t('common.noWallet') }}
        <router-link :to="'/create-wallet'" class="nounderline">{{ $t('common.createANewWallet') }}</router-link>
      </h5>
    </div>
    <div class="contents">
      <div class="tools">
        <div class="value-switch noselect">
          <div class="sliding-switch">
            <label class="switch">
              <input type="checkbox">
              <span class="slider round" @click="mnemonicValueBitSizeChange"/>
            </label>
            <div class="labels">
              <span :class="[!mnemonic24 ? 'white' : '', 'label-left']">12</span>
              <span :class="[mnemonic24 ? 'white' : '', 'label-right']">24</span>
            </div>
          </div>
        </div>
      </div>
      <div class="phrases">
        <ul>
          <li v-for="index in mnemonicSize" :key="index">
            <span>{{ index }}.</span>
            <input
              :tabindex="index"
              style="text-transform: lowercase;"
              type="text"
              name
              class="inputWord"
            >
          </li>
        </ul>
      </div>
      <div class="button-container">
        <button
          :disabled="!validMnemonic"
          :class="{ 'invalid-button': !validMnemonic }"
          class="mid-round-button-green-filled close-button"
          @click.prevent="unlockWallet"
        >
          <i v-show="creatingWallet" class="fa fa-spin fa-spinner fa-lg"/>
          <span v-show="!creatingWallet">{{ validMnemonic ? 'Confirm' : 'Words Are Incorrect' }}</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { MnemonicWallet } from '@/wallets';
import * as utils from '@/wallets/utils.js';
import { Toast } from '@/helpers';
import { mapGetters } from 'vuex';

export default {
  components: {},
  data() {
    return {
      mnemonicPhrase: new Array(this.mnemonicSize).fill(''),
      mnemonic24: false,
      mnemonicSize: 12,
      creatingWallet: false,
      validMnemonic: false,
      mnemonicStr: ''
    };
  },
  computed: {
    ...mapGetters({
      web3: 'web3'
    })
  },
  mounted() {
    this.$nextTick(
      function() {
        document.querySelectorAll('.phrases .inputWord').forEach(
          function(inputEl) {
            inputEl.addEventListener('input', this.inputHandler);
          }.bind(this)
        );
      }.bind(this)
    );
  },
  methods: {
    mnemonic() {
      let mnemonic = [];
      document.querySelectorAll('.phrases .inputWord').forEach(el => {
        mnemonic.push(el.value);
      });
      return mnemonic;
    },
    unlockWallet() {
      if (!this.validMnemonic) return;
      this.creatingWallet = true;

      MnemonicWallet(this.mnemonicStr, '')
        .then(wallet => {
          if (!this.web3.eth) this.$store.dispatch('setWeb3Instance');
          return wallet.getAccount(0);
        })
        .then(currentWallet => {
          currentWallet.mnemonic = this.mnemonicStr;
          this.$store.dispatch('decryptWallet', [currentWallet]);
          this.creatingWallet = false;
          this.$store.dispatch('setWalletNamespace', 'mobby');
          this.$router.push({ path: '/wallets/mobby' });
        })
        .catch(err => {
          Toast.responseHandler(err, Toast.ERROR);
          this.creatingWallet = false;
        });
    },
    mnemonicValueBitSizeChange() {
      this.mnemonic24 = !this.mnemonic24;
      this.mnemonic24 ? (this.mnemonicSize = 24) : (this.mnemonicSize = 12);
      this.mnemonicPhrase = new Array(this.mnemonicSize).fill('');
      this.validMnemonic = false;
    },
    inputHandler() {
      const mnemonic = this.mnemonic().map(word => word.toLowerCase()).join(' ');

      if (utils.validateMnemonic(mnemonic)) {
        this.validMnemonic = true;
        this.mnemonicStr = mnemonic
      } else {
        this.mnemonicStr = ''
        this.validMnemonic = false;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import 'AccessByMnemonicContainer.scss';
</style>
